.App {
  text-align: center;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto auto, auto auto;
}

/** Header styles ********************************/
.header {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  padding: 50px;
}
.section-one, .section-two {
  padding: 10px;
}
.section-one {
  padding: 0;
  position: relative;
}
.section-one h1 {
  font-family: Impact;
  color: black;
}
.section-two {
  position: relative;
  padding: 20px;
  border-right: 10px solid black;
  border-bottom: 10px solid black;
  -webkit-transform: rotatez(0deg);
      -ms-transform: rotate(0deg);
          transform: rotatez(0deg);
}
.section-two:before {
  content: '';
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: lightgray;
  z-index: -100;
  -webkit-transform: rotatez(-5deg);
      -ms-transform: rotate(-5deg);
          transform: rotatez(-5deg);
  
}
.section-two:after {
  content: '';
  position: absolute;
  top: 0; left: 0;
  width: 20%; height: 80%;
  border-left: 10px solid black;
  border-top: 10px solid black;
  z-index: -100;
  -webkit-transform: rotatez(3deg);
      -ms-transform: rotate(3deg);
          transform: rotatez(3deg);
  
}
.section-one h1 {

}
.section-one p {
  
}
/** Section Two - WEATHER Component *************/
.biggerText {
  color: black;
  font-family: Impact;
  font-size: 25px;
}
/** Section One - WeatherIcon Component *************/
.section-one img {
  width: 350px;
  margin-bottom: 20px;
  position: absolute;
  top: -40px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  opacity: 0.3;
  z-index: -10;
}


/** Form section ********************************/
.form-section {
  width: 100%;
  margin-bottom: 50px;
}
.form-section input {
  padding: 10px;
}
.form-section button {
  padding: 10px;
}
/** cities-panel *****************************/
.cities-panel {
  width: 70% !important;
  position: relative !important;
  float: left !important;
}

/** CityTile *****************************/

.row {
  position: absolute;
  width: 90%;
  position: absolute;
  left: 51%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  margin-top: 20px;
}

.cityTile h5 {
  font-size: 20px;
  
}

.cityTile .inner{
  display: block;
  min-height: 150px;
  background-size: 100%;
  background-position: bottom;
  background-repeat: no-repeat;
  
}
.cityTile {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  padding: 10px;
}

.cityTile .inner:before {
  content: '';
  position: absolute;
  top: 0; left: 0;
  width: 60%; height: 40%;
  //background: blue;
  z-index: -1;
  border-top: 5px solid rgba(0,0,0,0.1);
  border-left: 3px solid rgba(0,0,0,0.1);
  -webkit-transform: rotateZ(-3deg) scale(1);
      -ms-transform: rotate(-3deg) scale(1);
          transform: rotateZ(-3deg) scale(1);
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.cityTile .inner:after {
  content: '';
  position: absolute;
  bottom: 5px; right: 3px;
  width: 30%; height: 40%;
  //background: blue;
  z-index: -1;
  border-bottom: 3px solid rgba(0,0,0,0.1);
  border-right: 4px solid rgba(0,0,0,0.1);
  -webkit-transform: rotateZ(-3deg) scale(1);
      -ms-transform: rotate(-3deg) scale(1);
          transform: rotateZ(-3deg) scale(1);
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.cityTile .inner:hover:before {
  border-top: 5px solid rgba(0,0,0,0.8);
  border-left: 3px solid rgba(0,0,0,0.8);
  -webkit-transform: rotateZ(-5deg) scale(1.4);
      -ms-transform: rotate(-5deg) scale(1.4);
          transform: rotateZ(-5deg) scale(1.4);
  
}
.cityTile .inner:hover:after {
  border-bottom: 3px solid rgba(0,0,0,0.8);
  border-right: 4px solid rgba(0,0,0,0.8);
  -webkit-transform: rotateZ(5deg) scale(1.5);
      -ms-transform: rotate(5deg) scale(1.5);
          transform: rotateZ(5deg) scale(1.5);
  
}
.cityTile:hover {
  background: lightgray;
  -webkit-transform: rotateZ(2deg);
      -ms-transform: rotate(2deg);
          transform: rotateZ(2deg);
}


/** Footer STYLES *****************************/
.footer {
  background: lightgray;
  
}

/** MOBILE STYLES *****************************/

@media screen and (max-width: 768px) {
  .header {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
    padding: 50px;
  }
  .cityTile h5 {
    background: rgba(255,255,255,0.4);
  }
  form {
    padding: 0 20px;
  }
  .section-one {
    min-height: 280px;
  }
  .form-section input {
    width: 100%;
  }
  .form-section button {
    width: 100%;
  }
}


